import Vue from 'vue'
import Router from 'vue-router'
import Mulybuy from '@/page/mulybuy'
import Sgrouprule from '@/page/sgrouprule'
import Buy from '@/page/buy'
import Success from '@/page/success'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/buy',
      name: 'buy',
      component: Buy,
      meta: {
        title:''
      }
    },
    {
      path: '/',
      name: 'mulybuy',
      component: Mulybuy,
      meta: {
        title:''
      }
    },
    {
      path: '/sgrouprule',
      name: 'sgrouprule',
      component: Sgrouprule,
      meta: {
        title:'团购规则'
      }
    },
    {
      path: '/success',
      name: 'success',
      component: Success,
      meta: {
        title:''
      }
    },
    
  ]
})



